import { Component, OnInit, ChangeDetectorRef, NgZone, ViewChild } from '@angular/core';
import { MediaMatcher } from '@angular/cdk/layout';
import { Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  title = 'Overwatch';
  mobileQuery: MediaQueryList;
  @ViewChild('sidenav') sidenav;
  // openSidebar = false;

  constructor(private changeDetectorRef: ChangeDetectorRef, private media: MediaMatcher, private ngZone: NgZone, private router: Router) {}

  ngOnInit(): void {
    this.mobileQuery = this.media.matchMedia('(max-width: 600px)');
    this.mobileQuery.addEventListener<'change'>('change', (mqle => {
      if (!mqle.matches && this.sidenav.opened) {
        this.toggleSidenav();
      }
      this.changeDetectorRef.detectChanges();
    }));
  }

  toggleSidenav() {
    this.sidenav.toggle();
  }

  // toggleSidebar() { // deprecated
  //   this.openSidebar = !this.openSidebar;
  //   this.changeDetectorRef.detectChanges();
  // }
}
