import { Component, OnInit, Inject } from '@angular/core';
import { HeroesService } from '../heroes.service';
import { Hero } from '../casting/hero';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material';

@Component({
  selector: 'app-heroes-dialog',
  templateUrl: './heroes-dialog.html',
  styleUrls: ['./heroes-dialog.scss']
})
// tslint:disable-next-line: component-class-suffix
export class HeroesDialog {

  constructor(
    public dialogRef: MatDialogRef<HeroesDialog>,
    @Inject(MAT_DIALOG_DATA) public data: Hero) {}
}


@Component({
  selector: 'app-heroes',
  templateUrl: './heroes.component.html',
  styleUrls: ['./heroes.component.scss']
})
export class HeroesComponent implements OnInit {
  heroes: Hero[];
  amountHeroesPic: number;
  amountHeroesPicLoaded = 0;
  allHeroesPicLoaded: boolean;

  constructor(private heroesService: HeroesService, public dialog: MatDialog) { }

  ngOnInit() {
    this.heroesService.getHeroes().subscribe(res => {
      this.heroes = res;
      this.amountHeroesPic = res.length;
    });
  }

  picsLoaded() {
    this.amountHeroesPicLoaded++;
    if (this.amountHeroesPic <= this.amountHeroesPicLoaded) {
      this.allHeroesPicLoaded = true;
    }
  }

  openHeroDetails(hero: Hero) {
    this.dialog.open(HeroesDialog, {
      width: '500px',
      data: hero
    })
  }

}